import  React from 'react';
import styled from 'styled-components';
import type { ImageData } from '../../../mo/models';
import getStyle from '../../utils/calcComplexImage';
import { IMAGE_DATA_WRAPPER_CLASS_NAME, IMAGE_EDIT_BORDER } from '../../constants';
import type { IImageProps } from './types';
import { MetaImage } from './MetaImage';

const MetaImgRotate = styled.div`
  position: relative;
`;

export const MetaImgFrame = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  &::after {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-color: transparent;
    pointer-events: none;
    content: '';
  }
`;

export const ComplexImage: React.FC<IImageProps> = (props) => {
  const { data, isAutoFit, style: styleProp, ...rest } = props;
  const { rotateStyle, frameStyle, imgStyle } = getStyle(data, isAutoFit);

  return (
    <MetaImgRotate
      data-testid="image-complex-wrapper"
      className='cangjie-image-wrapper cangjie-image-complex-wrapper'
      style={rotateStyle}
    >
      <MetaImgFrame
        style={frameStyle}
        className={`${IMAGE_DATA_WRAPPER_CLASS_NAME} ${IMAGE_EDIT_BORDER}`}
      >
        <MetaImage
          data={data}
          style={{
            ...styleProp,
            ...imgStyle,
          }}
          {...rest}
        />
      </MetaImgFrame>
    </MetaImgRotate>
  );
};